.Weatherapp {
  margin: 0;
  padding: 0;
  height: 100vh;
  padding-top: 50px;   
  background-image: url("./backgroundimage.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;   
  box-sizing: border-box;
}

.WeatherGrid
{
  width: 40%;
  display: grid;
  grid-template-columns: 49% 49%;
  grid-column-gap: 2%;
  margin: auto;
  text-align: center;
  color: white;  
}

.mainWindow{
  font-size: 3em;
  background-color: rgba(0, 0, 0, 0.1);
}
.mainWindow div{
  margin-top: 25px;

}

.mainWindow img{
  height: 75px;
  width: 75px;
  margin-top: 25px;
 
}

.forecastGrid {
  height: 20%;
  width: 100%;
  display: grid;
  grid-template-columns: auto auto auto;
  color: white;
  background-color: rgba(0, 0, 0, 0.1);
  font-size: 2em;
  text-align: center;
  margin-bottom: 5px;
  margin-right: 15px;
}

.forecastItemInfo {
  margin-top: 13px;
  font-size: 1.8em;
}
.forecastItemWeather {
  margin-top: 25px;
}
.forecastItemDate {
  font-size: 0.6em;
}
.forecastItemDay {
  margin-top: 12px;
  font-size: 1.1em;
}

.weatherImage {
  height: 55px;
  width: 55px;
}

.inputDiv {
  transition: 1s;
  margin-top: 15px;
  margin-left: 30%;
}
.cityInput {
  text-align: center;
  font-size: 30px;
  width: 20%;
  color: white;
  margin-right: 15px;
  border: 2px rgba(0, 0, 0, 0.5) solid;
  background-color: rgba(7, 7, 7, 0.1);
  box-sizing: border-box;
  opacity: 0.3;
}

.cityInput:focus {
  outline: none;
  background-color: rgba(7, 7, 7, 0.7);
  opacity: 1;
}

.findCityBtn {
  border: 2px rgba(0, 0, 0, 0.5) solid;
  font-size: 30px;
  background-color: rgba(7, 7, 7, 0.1);
  color: rgb(105, 100, 100);
  transition: 0.5s;
  opacity: 0.3;
}

.findCityBtn:hover {
  color: white;
  border-color: rgba(7, 7, 7, 0.7);
  background-color: rgba(7, 7, 7, 0.7);
  opacity: 1;
}

.findCityBtn:focus {
  outline: none;
}

@media screen and (max-width: 1200px) {
  .WeatherGrid
  {
    width: 60%;    
  }

}

@media screen and (max-width: 850px) {
  .WeatherGrid
  {
    width: 80%;    
  }

}

@media screen and (max-width: 600px) {
  .WeatherGrid
  {
    width: 95%;    
  }

}

@media screen and (max-width: 500px) {
  .WeatherGrid
  {
    grid-template-columns: auto;
    grid-row-gap: 5px;        
  }

  .mainWindow{
    
    background-color: rgba(0, 0, 0, 0.1);
  }

  .mainWindow div{
    margin-top: 5px;
  
  }
  
  .mainWindow img{
    height: 75px;
    width: 75px;
    margin-top: 5px;
   
  }

  .inputDiv{
    margin: 0;
    display: flex;
    flex-direction: column;
    
    align-items: center;
  }
  .cityInput {
    width: 75%; 
    margin-bottom: 5px;    
  }

  .cityInput:focus
  {
    width: 100%;
  }

}